<template>
  <section class="content" id="content">
    <article class="markdown-section" id="main">
      <div v-html="content"></div>
      <div id="gitalk-container"></div>
    </article>
  </section>
</template>

<script>
import 'gitalk/dist/gitalk.css'
import Gitalk from 'gitalk'
export default {
  name: 'HelloWorld',
  props: {
    title: String,
  },
  data(){
    return {
      input: '',
    }
  },
  watch : {
    '$route'()
    {
      this.getPost ()
    }
  },
  methods: {
    getPost: function () {
        this.apiSetting.getPost.url = this.$route.path + '.md';
        this.request(this.apiSetting.getPost).then(
            res => {
              this.input = res.data
            },
            error => {
              this.input = error.status
              console.log(error)
            })
    },
    addClass: function (elements,cName) {
        elements.className += " " + cName;
    },
    gitalk: function () {
      var config = this.config;
      // console.log(config)
      var github = config.github;
      var id = github.raw;
      // console.log(document.getElementsByTagName('h1')[1])

      console.log('id:' + github.raw + this.$route.path + '.md')
      const gitalk = new Gitalk({
        clientID: github.clientID,
        clientSecret: github.clientSecret,
        repo: github.repo,
        owner: github.owner,
        admin: [github.owner],
        // id: location.pathname,      // Ensure uniqueness and length less than 50
        id: id + this.$route.path + '.md',      // Ensure uniqueness and length less than 50
        labels: [github.owner],
        // title: document.getElementsByTagName('h1')[0].innerHTML,
        distractionFreeMode: false  // Facebook-like distraction free mode
      })

      gitalk.render('gitalk-container')
    }

  },
  mounted: function ()
  {
    this.getPost ()
    this.gitalk ()
  },
  computed: {
    content: function () {
      return this.marked(this.input, { sanitize: true })
    }
  }

}
</script>

